import HTTPClient from "../base/http/HTTPClient";
import EventBus from "../base/EventBus/EventBus";
import eventBus from "../base/EventBus/EventBus";


const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Label)
    label: cc.Label = null;

    @property
    text: string = 'hello';

    // LIFE-CYCLE CALLBACKS:

    async onLoad () {
       let data = await HTTPClient._data();
       console.log(data,'55');
       eventBus.$emit("change",{data:1234})
       eventBus.$emit("change1",{data:12345})
    }
    start () {

    }

    // update (dt) {}
}
